.TodoSearch{
    background: #f9fbfc;
    border-radius: 10px;
    border: 2px solid #202329;
    margin: 0;
    height: 30px;
    width: 100%;
    font-size: 24px;
    text-align: center;
    font-family: montserrat;
    font-weight: 400;
    color: #1e1e1f;
    box-shadow: 0 5px 50px rgba(32,35,41,.25);
}
.TodoSearch::placeholder{
    color: #a5a5a5;
    font-family: montserrat;
    font-weight: 400
}
.TodoSearch:focus{
    outline-color: #61dafa
}